import initSqlJs from 'sql.js/dist/sql-wasm.js'
import dbUtils from './_statements'

let SQL = null
const sqlModuleReady = initSqlJs().then(sqlModule => { SQL = sqlModule })

function _getDataSourcesFromSqlResult (sqlResult) {
  if (!sqlResult) {
    return {}
  }
  const dataSorces = {}
  sqlResult.columns.forEach((column, index) => {
    dataSorces[column] = sqlResult.values.map(row => row[index])
  })
  return dataSorces
}

export default class Sql {
  constructor () {
    this.db = null
  }

  static build () {
    return sqlModuleReady
      .then(() => {
        return new Sql()
      })
  }

  createDb (buffer) {
    if (this.db != null) this.db.close()
    this.db = new SQL.Database(buffer)
    this.db.create_function('util_url_load', (url, timeout, auth) => {
      function getURL () {
        const xhr = new XMLHttpRequest()
        xhr.open('GET', url, false)
        xhr.timeout = timeout
        xhr.withCredentials = auth === 'true'
        xhr.responseType = 'text'
        xhr.send(null)
        return xhr
      }

      try {
        const xhr = getURL()
        if (xhr.readyState > 1 && xhr.readyState < 4) {
          if (xhr.status < 200 || xhr.status >= 300) {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'HttpStatusException')
          }
        } else if (xhr.readyState === 4) {
          if (xhr.status >= 200 && xhr.status < 300) {
            return xhr.responseText
          } else {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'NetworkStatusException')
          }
        }
      } catch (e) {
        return Error(`${e.name}: ${e.message}`)
      }
      return Error('Unknown Error')
    })
    this.db.create_function('util_url_postload', (filename, url, timeout, auth, jsondata) => {
      function postURL () {
        const xhr = new XMLHttpRequest()
        xhr.open('POST', url, false)
        // xhr.setRequestHeader('Content-Type', 'application/json;charset=UTF-8')
        xhr.timeout = timeout
        xhr.withCredentials = auth === 'true'
        xhr.responseType = 'arraybuffer'
        xhr.send(JSON.stringify(jsondata))
        return xhr
      }
      try {
        const xhr = postURL()
        if (xhr.readyState > 1 && xhr.readyState < 4) {
          if (xhr.status < 200 || xhr.status >= 300) {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'HttpStatusException')
          }
        } else if (xhr.readyState === 4) {
          if (xhr.status >= 200 && xhr.status < 300) {
            return xhr.response
          } else {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'NetworkStatusException')
          }
        }
      } catch (e) {
        return Error(`${e.name}: ${e.message}`)
      }
      return Error('Unknown Error')
    })
    this.db.create_function('util_url_getfile', (filename, url, timeout, auth) => {
      const self = this
      function getURL () {
        const xhr = new XMLHttpRequest()
        xhr.open('GET', url, false)
        xhr.timeout = timeout
        xhr.withCredentials = auth === 'true'
        xhr.responseType = 'arraybuffer'
        xhr.send(null)
        return xhr
      }
      try {
        const xhr = getURL()
        if (xhr.readyState > 1 && xhr.readyState < 4) {
          if (xhr.status < 200 || xhr.status >= 300) {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'HttpStatusException')
          }
        } else if (xhr.readyState === 4) {
          if (xhr.status >= 200 && xhr.status < 300) {
            self.db.writefile(filename, xhr.response)
            return 0
          } else {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'NetworkStatusException')
          }
        }
      } catch (e) {
        return Error(`${e.name}: ${e.message}`)
      }
      return Error('Unknown Error')
    })
    this.db.create_function('util_url_postfile', (filename, url, timeout, auth, jsondata) => {
      const self = this
      function postURL () {
        const xhr = new XMLHttpRequest()
        xhr.open('POST', url, false)
        // xhr.setRequestHeader('Content-Type', 'application/json;charset=UTF-8')
        xhr.timeout = timeout
        xhr.withCredentials = auth === 'true'
        xhr.responseType = 'arraybuffer'
        xhr.send(JSON.stringify(jsondata))
        return xhr
      }
      try {
        const xhr = postURL()
        if (xhr.readyState > 1 && xhr.readyState < 4) {
          if (xhr.status < 200 || xhr.status >= 300) {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'HttpStatusException')
          }
        } else if (xhr.readyState === 4) {
          if (xhr.status >= 200 && xhr.status < 300) {
            self.db.writefile(filename, xhr.response)
            return 0
          } else {
            throw new DOMException(`Check console. Status: ${xhr.status} ReadyState: ${xhr.readyState}`, 'NetworkStatusException')
          }
        }
      } catch (e) {
        return Error(`${e.name}: ${e.message}`)
      }
      return Error('Unknown Error')
    })
    this.db.create_function('util_readfile', (filename) => {
      const self = this
      const filetext = self.db.readfile(filename)
      return filetext
    })
    this.db.create_function('util_readdir', (path) => {
      const self = this
      const filetext = self.db.readdir(path)
      return filetext
    })
    this.db.create_function('util_deletefile', (path) => {
      const self = this
      self.db.deletefile(path)
    })
    return this.db
  }

  open (buffer) {
    this.createDb(buffer && new Uint8Array(buffer))
    return {
      ready: true
    }
  }

  exec (sql, params) {
    if (this.db === null) {
      this.createDb()
    }
    if (!sql) {
      throw new Error('exec: Missing query string')
    }
    const sqlResults = this.db.exec(sql, params)
    return sqlResults.map(result => {
      return {
        columns: result.columns,
        values: _getDataSourcesFromSqlResult(result)
      }
    })
  }

  writefile (filename, filedata) {
    if (this.db === null) {
      this.createDb()
    }
    this.db.writefile(filename, filedata)
  }

  import (tabName, data, progressCounterId, progressCallback, chunkSize = 1500) {
    if (this.db === null) {
      this.createDb()
    }
    const columns = data.columns
    const rowCount = data.values[columns[0]].length
    this.db.exec(dbUtils.getCreateStatement(tabName, data.values))
    const chunks = dbUtils.generateChunks(data.values, chunkSize)
    const chunksAmount = Math.ceil(rowCount / chunkSize)
    let count = 0
    const insertStr = dbUtils.getInsertStmt(tabName, columns)
    const insertStmt = this.db.prepare(insertStr)

    progressCallback({ progress: 0, id: progressCounterId })
    for (const chunk of chunks) {
      this.db.exec('BEGIN')
      for (const row of chunk) {
        insertStmt.run(row)
      }
      this.db.exec('COMMIT')
      count++
      progressCallback({ progress: 100 * (count / chunksAmount), id: progressCounterId })
    }

    return {
      finish: true
    }
  }

  export () {
    return this.db.export()
  }

  close () {
    if (this.db) {
      this.db.close()
    }
    return {
      finished: true
    }
  }
}
